@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,300;0,400;0,500;0,600;0,700;0,900;1,300;1,400;1,500;1,600&display=swap);
/* Main is for all the background color */
/* Primary is for all the text we need to be highlighted */
/* Assent is secondary color to text color */
/* Text is main color */
/* Line is for dividers we might need to add */
:root{
  --main: rgb(24,24,24);
  --primary:orange;
  --assent:rgb(255,255,255);
  --text:rgb(91,153,93);
  --line:rgb(45,45,45);
  --font:'Source Code Pro', monospace;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(24,24,24);
  background-color: var(--main);
  color: rgb(255,255,255);
  color: var(--assent);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
a{
  text-decoration: none;
  color: inherit;
}

@font-face {
  font-family: "KiwiMaru";
  src: local("KiwiMaru"),
    url(/stonks-frontend/static/media/KiwiMaru.25a69c57.ttf) format("truetype");
  font-weight: normal;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.AboutPage {
    font-family: KiwiMaru;
}

.AboutPage p{
    font-size: large
}

.H1 {
    font-size: 300%;
    padding-top: 10%;
    padding-left: 10%;
}

.tagLine {
    padding-left: 27.5%;
    padding-bottom: 5%;
}

.ProjectInformation {
    width: 50%;
    border: aliceblue;
    border-style:groove;
    border-radius: 20px;
    padding: 20px;
}

.Mod {
    padding-left: 60%;
    padding-top: 5%;
    width: 80%;
}

ul {
    list-style-type: disc;
}

.Moderators {
    padding-left: 40%;
    padding: 20% 20% 20% 20%;
    border: aliceblue;
    border-style:groove;
    border-radius: 20px;
}

.Members {
    padding-left: 5%;
    padding-top: 5%;
    width: 80%;
}

.Member {
    width: 30%;
    padding: 5% 5% 5% 5%;
    border: aliceblue;
    border-style:groove;
    border-radius: 20px;
}


.dash{
    display: grid;
    grid-template-columns: 1fr 3fr;
}


.header{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    background-color: var(--main);
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.8);
    padding: 15px 60px;
    color: var(--text);
    font-family: var(--font);
}
.logo{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.logo>p{
    margin-left: 10px;
    font-size: 1.7em;
    font-weight: bold;
}
.logo>img{
    width: 50px;
}
.header-options{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.header-options>a>p{
    padding: 4px 15px;
    margin: 10px;
    font-size: 1.2em;
}
.header-options>a>p:hover{
    border-bottom: 2px solid white;
    padding-bottom: 2px;
}
.active{
    border-bottom: 2px solid white;
    padding-bottom: 2px;
}
